// 星星组件
import star from '@/components/layout/star.vue'	
export default {
	props: {
		canteenListData: {
			type: Array,
			default() {
				return []
			}
		}
	},
	data () {
		return {
			// 星星评分
			scoreNum: 4.3
		}
	},
	methods: {
		opencanteenInfoPage(id) {
			uni.navigateTo({
				// 跳转路径
				// 摊位列表页
				url: '../../pages/canteen-list-page/canteen-list-page?id='+id
			});
		}
	},
	components: {
		star
	}
}
